<template>
  <div class="wrapper">
    <el-button type="primary" @click="joinRoom"></el-button>
    <el-button type="primary" @click="leaveRoom"></el-button>

    <!---->
    <div class="change-mode">
      <el-radio-group v-model="mode">
        <el-radio-button label="living"></el-radio-button>
        <el-radio-button label="videoPhone"></el-radio-button>
        <el-radio-button label="shareScreen"></el-radio-button>
      </el-radio-group>
    </div>

    <!---->
    <div class="change-role" v-if="mode==='living'">
      <el-radio-group v-model="role">
        <el-radio-button label="anchor" @click="changeRole"></el-radio-button>
        <el-radio-button label="audience" @click="changeRole"></el-radio-button>
      </el-radio-group>
    </div>

    <div id="local"></div>
    <div id="remote"></div>

  </div>
</template>

<script>
import TRTC from 'trtc-js-sdk';
import {genTestUserSig} from "../../public/debug/GenerateTestUserSig";

export default {
  name: "living",

  data() {
    return {
      client: null,
      option: genTestUserSig(this.$store.state.userId),
      userId: this.$store.state.userId,
      roomId: this.$store.state.roomId,
      role: 'audience',
      mode: 'living',
    }
  },

  methods: {

    async joinRoom() {
      // 
      this.client = TRTC.createClient({
        mode: this.mode,
        sdkAppId: this.option.sdkAppID,
        userId: this.userId,
        userSig: this.option.userSig,
        useStringRoomId: true,
      });

      this.client.on('stream-added', event => {
        const remoteStream = event.stream;
        console.log(': ' + remoteStream.getId());

        //
        this.client.subscribe(remoteStream);
      });
      this.client.on('stream-subscribed', event => {
        const remoteStream = event.stream;
        console.log('' + remoteStream.getId());

        // 
        remoteStream.play('remote');
      });

      // 
      await this.client.join({
        roomId: this.roomId,
        role: this.role,
      })

      if (!(this.role === 'audience' && this.mode === 'living')){
        // 
        // eslint-disable-next-line no-unused-vars
        const localStream = TRTC.createStream({userId: this.userId, audio: true, video: true});
        await localStream.initialize();
        // 
        this.client
            .publish(localStream)
            .catch(error => {
              console.error(' ' + error);
            })
            .then(() => {
              console.log('');
              localStream.play('local')
            });
      }


    },

    //
    changeRole() {
      this.client
          .switchRole(this.role)
          .catch(error => {
            console.error(' ' + error);
          })
          .then(() => {
            // 
          });
    },

    // 
    leaveRoom() {
      this.client.leave().catch(error => {
        console.log(error);
      }).then(() => {
        console.log('');
      })
    },

  },

  mounted() {
  },

}
</script>

<style scoped>

#local {
  width: 100px;
  height: 100px;
}

#remote {
  width: 100px;
  height: 100px;
}

</style>
